







import { Component, Vue } from 'vue-property-decorator';

@Component
export default class JtcHeader extends Vue {
    me!: any;

    get logined() {
        return !!this.me;
    }
    get loginUrl() {
        return "https://japanisetextclassifierfunction.azurewebsites.net/.auth/login/twitter?post_login_redirect_url=" + encodeURIComponent(window.location.toString());
    }

    public async created() {
        try {
            const response = await fetch("https://japanisetextclassifierfunction.azurewebsites.net/.auth/me");
            console.log(response);
            if (response.ok) {
                this.me = await response.json();
                console.log(this.me);
            } else {
                this.me = null;
            }
        } catch (err) {
            console.error(err);
            this.me = null;
        }
    }
}
